import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';

// constants
import {ACTIONS} from './constants';

// **** Critical Incidents ****
// In order to get critical incidents count,
// we are getting the aggregate count of the records of 'incident' table with following query conditions :
// ci = current CI and priority as 1 (critical) and active as true
const criticalIncidentsQuery = `
	query($queryConditions: String!){
		criticalIncidentsQuery: GlideAggregate_Query(tableName: "incident", queryConditions: $queryConditions){
			count
		}
	}
`;

const fetchCriticalIncidentsEffect = createGraphQLEffect(
	criticalIncidentsQuery,
	{
		variableList: ['queryConditions'],
		startActionType: ACTIONS.HEALTH_CRITICAL_INCIDENTS_QUERY_STARTED,
		successActionType: ACTIONS.HEALTH_CRITICAL_INCIDENTS_QUERY_SUCCEEDED,
		errorActionType: ACTIONS.HEALTH_CRITICAL_INCIDENTS_QUERY_FAILED
	}
);

// **** Total Incidents ****
// In order to get total incidents count,
// we are getting the aggregate count of the records of 'incident' table with following query conditions :
// ci = current CI and active as true
const totalIncidentsQuery = `
	query($queryConditions: String!){
		totalIncidentsQuery: GlideAggregate_Query(tableName: "incident", queryConditions: $queryConditions){
			count
		}
	}
`;

const fetchTotalIncidentsEffect = createGraphQLEffect(totalIncidentsQuery, {
	variableList: ['queryConditions'],
	startActionType: ACTIONS.HEALTH_TOTAL_INCIDENTS_QUERY_STARTED,
	successActionType: ACTIONS.HEALTH_TOTAL_INCIDENTS_QUERY_SUCCEEDED,
	errorActionType: ACTIONS.HEALTH_TOTAL_INCIDENTS_QUERY_FAILED
});

