// constants
import {ACTIONS, VIEW_DATA} from './constants';

export function healthFetchDataEffect(coeffects) {
	const {state, dispatch} = coeffects;
	// TODO: DOES ALL THE DATA NEED TO BE LOADED UP FRONT OR CAN IT BE LOADED AS THE PAGE/VIEW IS CHANGED?
	const {table, sysid} = state.properties;

	dispatch(ACTIONS.HEALTH_CRITICAL_INCIDENTS_FETCH, {
		queryConditions: `cmdb_ci=${sysid}^priority=1^active=true`
	});
	dispatch(ACTIONS.HEALTH_TOTAL_INCIDENTS_FETCH, {
		queryConditions: `cmdb_ci=${sysid}^active=true`
	});
	dispatch(ACTIONS.HEALTH_CRITICAL_CHANGES_FETCH, {
		queryConditions: `cmdb_ci=${sysid}^priority=1^EQ^active=true`
	});
	dispatch(ACTIONS.HEALTH_TOTAL_CHANGES_FETCH, {
		queryConditions: `cmdb_ci=${sysid}^active=true`
	});
	dispatch(ACTIONS.HEALTH_REQ_ATTR_STATUS_FETCH, {
		queryConditions: 'metric=bac72b5137130200f212cc028e41f1b8^active=true'
	});

	dispatch(ACTIONS.HEALTH_NON_COMPLIANT_REL_STATUS_FETCH, {
		queryConditions: 'metric=ad3dd754cb37030061867f1b734c9c2d^active=true'
	});
	dispatch(ACTIONS.HEALTH_STALE_REL_STATUS_FETCH, {
		queryConditions: 'metric=d4772b5137130200f212cc028e41f16c^active=true'
	});
	dispatch(ACTIONS.HEALTH_REQ_ATTR_COUNT_FETCH, {
		queryConditions: `metric=bac72b5137130200f212cc028e41f1b8^ci=${sysid}`
	});
	dispatch(ACTIONS.HEALTH_TOTAL_ATTR_COUNT_FETCH, {
		table: table
	});
	dispatch(ACTIONS.HEALTH_NON_COMPLIANT_REL_COUNT_FETCH, {
		queryConditions: `parent_class=${table}^ORchild_class=${table}^metric=ad3dd754cb37030061867f1b734c9c2d^active=true^to_delete=false`,
		sysId: sysid
	});
	dispatch(ACTIONS.HEALTH_TOTAL_REL_COUNT_FETCH, {
		queryConditions: `parent=${sysid}^ORchild=${sysid}`
	});
	dispatch(ACTIONS.HEALTH_STALE_REL_COUNT_FETCH, {
		queryConditions: `parent=${sysid}^ORchild=${sysid}`,
		pagination: {limit: 1000, offset: 0}
	});
}

// **** start Critical Incidents ****
export function criticalIncidentsQueryStartedEffect(coeffects) {
	const {updateState} = coeffects;
	updateState({criticalIncidentsLoading: true});
}

export function criticalIncidentsQuerySucceededEffect(coeffects) {
	const {action, updateState} = coeffects;
	updateState({
		criticalIncidentsCount: action.payload.data.criticalIncidentsQuery[0].count,
		criticalIncidentsErrors: false,
		criticalIncidentsLoading: false
	});
}

export function criticalIncidentsQueryFailedEffect(coeffects) {
	const {updateState} = coeffects;
	updateState({
		criticalIncidentsErrors: true,
		criticalIncidentsLoading: false
	});
}
// **** end Critical Incidents ****

