export function handleBareButtonClickEffect(coeffects) {
	const {dispatch, action, state, updateState} = coeffects;
	const {pageAction} = action.payload;
	const {displayIndex} = state;

	// Note: Can add more actions here as needed for future enhancements
	if (pageAction === VIEW_DATA.VIEW_DASHBOARD) {
		dispatch(ACTIONS.HEALTH_OPEN_CI_DASHBOARD);
	} else if (pageAction === VIEW_DATA.PREVIOUS_PAGE) {
		// set the active display
		updateState({
			displayIndex: displayIndex === 0 ? 4 : displayIndex - 1
		});
	} else if (pageAction === VIEW_DATA.NEXT_PAGE) {
		// set the active display
		updateState({
			displayIndex: displayIndex === 4 ? 0 : displayIndex + 1
		});
	}
}

export function setDisplayIndexEffect(coeffects) {
	const {action, state, updateState} = coeffects;
	const {newIndex} = action.payload;
	const {displayIndex} = state;

	// no need to rerender if already active
	if (displayIndex === newIndex) {
		return;
	}

	// set the active display
	updateState({displayIndex: newIndex});
}

export function openCiDashboardEffect(coeffects) {
	const {state} = coeffects;
	const {sysid, table} = state.properties;
	window.open(
		`/cmdb_dashboard.do?sysparm_record=${sysid}&sysparm_table=${table}&sysparm_doctype=true&sysparm_editable=true&sysparm_view=ci_dashboard`
	);
}

export function componentReadyEffect(coeffects) {
	const {dispatch} = coeffects;
	dispatch(ACTIONS.HEALTH_FETCH_DATA);
}

export function componentPropertyChangedEffect(coeffects) {
	const {action, dispatch} = coeffects;

	// refetch data and set to displayIndex 0 if the sysid is changed
	const {name, previousValue} = action.payload;

	if (name === 'sysid' && previousValue) {
		dispatch(ACTIONS.HEALTH_FETCH_DATA);
		dispatch(ACTIONS.HEALTH_SET_DISPLAY_INDEX, {newIndex: 0});
	}
}
