import {uniqWith} from 'lodash';
import {t} from 'sn-translate';

// constants
import {ERROR_CODES, VIEW_DATA} from './constants';

export function getPageDisplayMessage(msgType, icon) {
	switch (msgType) {
		case VIEW_DATA.DATA_LOADING:
			return t('Loading Relationships...');
		case ERROR_CODES.DATA_LOADING_ERROR:
			return t('Data transaction timed out. Please refresh the page.');
		case ERROR_CODES.DATA_TOO_LONG:
			return t(
				'The max number of relationships that can be displayed has been reached. To view the complete list, open the CI Dashboard.'
			);
		default:
			return t('No data available.');
	}
}

export function getFormattedRelationships(relationshipData, sysid) {
	const relations = {};

	for (let index = 0; index < relationshipData.length; index++) {
		if (relationshipData[index].child._reference.sys_id.value === sysid) {
			let current =
				relations[
					relationshipData[index].type._reference.child_descriptor.value
				];

			if (!current) {
				current = {
					ciSysClass:
						relationshipData[index].parent._reference.sys_class_name.value,
					ciClassDisplayName:
						relationshipData[index].parent._reference.sys_class_name
							.displayValue,
					relationTypeDesc:
						relationshipData[index].type._reference.child_descriptor.value,
					relationTypeSysId:
						relationshipData[index].type._reference.sys_id.value,
					relations: []
				};

				relations[
					relationshipData[index].type._reference.child_descriptor.value
				] = current;
			}

			current.relations.push({
				childName: relationshipData[index].parent._reference.name.value,
				childSysClassName:
					relationshipData[index].parent._reference.sys_class_name.value,
				childSysId: relationshipData[index].parent._reference.sys_id.value,
				relationSysId: relationshipData[index].sys_id.value,
				relationTypeSysId: relationshipData[index].type._reference.sys_id.value
			});

			continue;
		}

		if (relationshipData[index].parent._reference.sys_id.value === sysid) {
			let current =
				relations[
					relationshipData[index].type._reference.parent_descriptor.value
				];

			if (!current) {
				current = {
					ciSysClass:
						relationshipData[index].child._reference.sys_class_name.value,
					ciClassDisplayName:
						relationshipData[index].child._reference.sys_class_name
							.displayValue,
					relationTypeDesc:
						relationshipData[index].type._reference.parent_descriptor.value,
					relationTypeSysId:
						relationshipData[index].type._reference.sys_id.value,
					relations: []
				};

				relations[
					relationshipData[index].type._reference.parent_descriptor.value
				] = current;
			}

			current.relations.push({
				childName: relationshipData[index].child._reference.name.value,
				childSysClassName:
					relationshipData[index].child._reference.sys_class_name.value,
				childSysId: relationshipData[index].child._reference.sys_id.value,
				relationSysId: relationshipData[index].sys_id.value,
				relationTypeSysId: relationshipData[index].type._reference.sys_id.value
			});
		}
	}

	let rels = Object.keys(relations).map((key) => relations[key]);
	return rels.filter((rel) => rel.ciClassDisplayName !== null);
}

export function getRelationshipsDisplayData(relationshipData) {
	let rels = [];
	let lines = 0;
	let totalPages = 0;
	let k = 0;
	let i = 0;
	const ciRelations = [];

	//dont show duplicate relationships
	while (i < relationshipData.length) {
		relationshipData[i].relations = uniqWith(
			relationshipData[i].relations,
			(a, b) =>
				a.relationTypeSysId + a.childSysId ===
				b.relationTypeSysId + b.childSysId
		);

		const currentRel = {
			relationTypeSysId: relationshipData[i].relationTypeSysId,
			relCount: relationshipData[i].relations.length,
			ciClassDisplayName: relationshipData[i].ciClassDisplayName,
			relationTypeDesc: relationshipData[i].relationTypeDesc,
			relations: []
		};

		if (lines === 5) {
			totalPages++;
			ciRelations.push(rels);
			rels = [];
			lines = 0;
		}

		lines++;

		for (let j = k; j < relationshipData[i].relations.length; j++, k++) {
			currentRel.relations.push(relationshipData[i].relations[j]);
			lines++;

			//only 5 items per page, for the 6th item go to the next page.
			if (lines === 6) {
				totalPages++;
				lines = 0;
				rels.push(currentRel);
				ciRelations.push(rels);
				rels = [];

				if (j < relationshipData[i].relations.length - 1) {
					k++;
				} else {
					i++;
					k = 0;
				}

				break;
			}
		}

		if (
			i < relationshipData.length &&
			k == relationshipData[i].relations.length
		) {
			i++;
			k = 0;
			rels.push(currentRel);
		}
	}

	if (rels && rels.length > 0) {
		totalPages++;
		ciRelations.push(rels);
	}

	return {ciRelations: ciRelations, totalPages: totalPages};
}
