import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {ACTIONS} from './constants';

// **** Relationships ****
const relationshipQuery = `
query($queryConditions: String!, $pagination: Glide_PaginationInputType){
    GlideRecord_Query {
        relationshipsLevelOne: cmdb_rel_ci(queryConditions: $queryConditions, pagination: $pagination ){
            _results{
                sys_id {
                    value
                }
                child {
                    _reference {
                        name{
                            value
                        }
                        sys_id {
                            value
                        }
                        sys_class_name {
                            value
                            displayValue
                        }
                    }
                }
                parent {
                    _reference {
                        name{
                            value
                        }
                        sys_id {
                            value
                        }
                        sys_class_name {
                            value
                            displayValue
                        }
                    }
                }
                type {
                    _reference {
                        parent_descriptor {
                            value
                        }
                        child_descriptor {
                            value
                        }
                        sys_id {
                            value
                        }
                    }
                }
            }
            _rowCount
        }
    }
}
`;

const relationshipQueryEffect = createGraphQLEffect(relationshipQuery, {
	variableList: ['queryConditions', 'pagination'],
	startActionType: ACTIONS.RELATIONSHIP_QUERY_STARTED,
	successActionType: ACTIONS.RELATIONSHIP_QUERY_SUCCEEDED,
	errorActionType: ACTIONS.RELATIONSHIP_QUERY_FAILED
});

export default {
	relationshipQueryEffect: relationshipQueryEffect
};
