import {t} from 'sn-translate';
import filterXSS from 'xss';

// constants
import {COLORS} from './constants';

export function getTimestampForNumberOfDays(days, add) {
	if (add) {
		return new Date().getTime() + getTimeInMillisForDays(days);
	}

	return new Date().getTime() - getTimeInMillisForDays(days);
}

export function formatJSDate(date) {
	const year = date.getFullYear();
	const month =
		date.getMonth() < 9 ? `0${date.getMonth() + 1}` : date.getMonth() + 1;
	const days = getDateNumber(date.getDate());
	const hours = getDateNumber(date.getHours());
	const minutes = getDateNumber(date.getMinutes());
	const seconds = getDateNumber(date.getSeconds());

	return `'${year}-${month}-${days}','${hours}:${minutes}:${seconds}'`;
}

export function getFilterForSysId(sysId) {
	return sysId ? btoa(`sys_id=${sysId}`).replace(/=/g, '-') : '';
}

export function getLoadingState(state) {
	const {historyLoading, incidentsLoading, requestsLoading} = state;
	return historyLoading || incidentsLoading || requestsLoading;
}

export function getFormattedActivityData(
	historyData,
	incidentsData,
	requestsData,
	timestamp
) {
	// Note: Don't need to filter here as query takes timestamp parameter
	const historyEvents = getHistoryAsEvents(historyData);
	const incidentEvents = getIncidentsAsEvents(
		filterEventItems(incidentsData, timestamp)
	);
	const requestEvents = getRequestsAsEvents(
		filterEventItems(requestsData, timestamp)
	);

	return [...historyEvents, ...incidentEvents, ...requestEvents];
}

export function safeParseInt(value) {
	const parsed = parseInt(value);
	return isNaN(parsed) ? 0 : parsed;
}

const getTimeInMillisForDays = (days) => {
	return 1000 * 60 * 60 * 24 * days;
};

const getDateNumber = (n) => {
	return n < 10 ? `0${n}` : n;
};

const getDeepCopy = (objToCopy) => {
	return objToCopy ? JSON.parse(JSON.stringify(objToCopy)) : objToCopy;
};

const getTimeZoneOffset = () => {
	return new Date().getTimezoneOffset() * 60 * 1000;
};

// format history to match timeline component json api
const getHistoryAsEvents = (historyData) => {
	if (historyData && historyData.length > 0) {
		const events = [];

		historyData.forEach((event) => {
			if (
				!event.entryItem.auditValues ||
				event.entryItem.auditValues.length === 0
			) {
				return;
			}

			const timestamp = new Date(
				event.sysCreatedOnAdjusted.replace(/-/g, '/')
			).getTime();

			event.entryItem.auditValues.forEach((value) => {
				if (value.fieldName === 'sys_updated_on') {
					return;
				}

				events.push({
					timestamp: timestamp,
					description: value.oldValue
						? t(
								'Changed {0}: {1} to {2}',
								value.fieldLabel,
								filterXSS(value.oldValue),
								filterXSS(value.newValue)
						  )
						: t('Set {0} to {1}', value.fieldLabel, filterXSS(value.newValue)),
					data: getDeepCopy(value)
				});
			});
		});

		if (events.length > 0) {
			return [
				{
					name: 'history',
					icon: 'recordActivityStreamFill',
					color: COLORS.TIMELINE_ICON_COLOR,
					label: t('History audit event'),
					labelPlural: t('History audit events'),
					events: events
				}
			];
		}
	}

	return [];
};

// format incidents to match timeline component json api
const getIncidentsAsEvents = (incidentsData) => {
	if (incidentsData && incidentsData.length > 0) {
		const events = incidentsData.map((incident) => {
			return {
				timestamp:
					new Date(incident.sys_created_on.value.replace(/-/g, '/')).getTime() -
					getTimeZoneOffset(),
				description: t(
					'{0} Incident {1} with Priority {2}.',
					incident.state.displayValue,
					incident.number.value,
					incident.priority.displayValue
				),
				data: getDeepCopy(incident)
			};
		});

		if (events && events.length > 0) {
			return [
				{
					name: 'incidents',
					icon: 'circleExclamationFill',
					color: COLORS.TIMELINE_ICON_COLOR,
					label: t('Incident event'),
					labelPlural: t('Incident events'),
					events: events
				}
			];
		}
	}

	return [];
};

// format requests to match timeline component json api
const getRequestsAsEvents = (requestsData) => {
	if (requestsData && requestsData.length > 0) {
		const events = requestsData.map((request) => {
			return {
				timestamp:
					new Date(request.sys_created_on.value.replace(/-/g, '/')).getTime() -
					getTimeZoneOffset(),
				description: t(
					'Created {0} - {1}',
					request.number.value,
					filterXSS(request.short_description.value)
				),
				data: getDeepCopy(request)
			};
		});

		if (events && events.length > 0) {
			return [
				{
					name: 'request',
					icon: 'changeFill',
					color: COLORS.TIMELINE_ICON_COLOR,
					label: t('Change request event'),
					labelPlural: t('Change request events'),
					events: events
				}
			];
		}
	}

	return [];
};

// filter out items that fall outside of display dates
const filterEventItems = (events, timestamp) => {
	return events.filter((event) => {
		const eventTime =
			new Date(event.sys_created_on.value.replace(/-/g, '/')).getTime() -
			getTimeZoneOffset();

		return eventTime > timestamp;
	});
};
