import {createAmbSubscriptionEffect} from '@servicenow/ui-effect-amb';

// constants
import {ACTIONS} from './constants';

// setup record watcher for reacting to record changes
// https://code.devsnc.com/pages/dev/sn-seismic/api-reference/effects/amb-effect.html
const recordWatcherEffect = createAmbSubscriptionEffect(
	'/rw/default/:table/:filter',
	{
		subscribeStartedActionType:
			ACTIONS.TIMELINE_RECORD_WATCHER_SUBSCRIBE_STARTED,
		subscribeSucceededActionType: ACTIONS.TIMELINE_RECORD_WATCHER_SUBSCRIBED,
		subscribeFailedActionType: ACTIONS.TIMELINE_RECORD_WATCHER_SUBSCRIBE_FAILED,
		unsubscribeSucceededActionType:
			ACTIONS.TIMELINE_RECORD_WATCHER_UNSUBSCRIBED,
		messageReceivedActionType: ACTIONS.TIMELINE_RECORD_WATCHER_ITEM_CHANGED
	}
);

export default {
	recordWatcherEffect: recordWatcherEffect
};
