import {createCustomElement, actionTypes} from '@servicenow/ui-core';

// action handlers
import * as actionHandlers from './action-handlers';

// html
import styles from './styles.scss';
import view from './view';

// component lifecycle actions
const {
	COMPONENT_DOM_READY,
	COMPONENT_CONNECTED,
	COMPONENT_DISCONNECTED,
	COMPONENT_PROPERTY_CHANGED
} = actionTypes;

// graphQL
import queries from './graphql-effects.js';

// AMB
import amb from './amb-effects.js';

// attempt to stop repeating common config options everywhere
const actionHandlersCommonConfig = {
	stopPropagation: true,
	private: true
};

export default createCustomElement('sn-cmdb-timeline-card', {
	styles,
	renderer: {
		view: view
	},
	initialState: {
		activityDataKey: null,
		timestamp: 0,
		historyResponseData: null,
		historyLoading: true,
		historyError: false,
		incidentsResponseData: null,
		incidentsResponseCount: 0,
		incidentsLoading: true,
		incidentsError: false,
		requestsResponseData: null,
		requestsResponseCount: 0,
		requestsLoading: true,
		requestsError: true
	},
	properties: {
		table: {
			default: ''
		},
		sysid: {
			default: ''
		},
		name: {
			default: ''
		},
		days: {
			default: 14
		},
		active: {
			default: true
		},
		isVisible: {
			default: true
		},
		// Not actually referenced in the code
		setTabsProps: {
			default: null
		}
	},
	actionHandlers: {
		TIMELINE_SET_UNIQUE_LOCALS: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.timelineSetUniqueLocalsEffect
		},
		TIMELINE_FETCH_DATA: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.timelineFetchDataEffect
		},
		TIMELINE_HISTORY_FETCH: queries.fetchHistoryEffect,
		TIMELINE_HISTORY_QUERY_STARTED: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.timelineHistoryQueryStartedEffect
		},
		TIMELINE_HISTORY_QUERY_SUCCEEDED: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.timelineHistoryQuerySucceededEffect
		},
		TIMELINE_HISTORY_QUERY_FAILED: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.timelineHistoryQueryFailedEffect
		},
		TIMELINE_INCIDENTS_FETCH: queries.fetchIncidentsEffect,
		TIMELINE_INCIDENTS_QUERY_STARTED: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.timelineIncidentsQueryStartedEffect
		},
		TIMELINE_INCIDENTS_QUERY_SUCCEEDED: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.timelineIncidentsQuerySucceededEffect
		},
		TIMELINE_INCIDENTS_QUERY_FAILED: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.timelineIncidentsQueryFailedEffect
		},
		TIMELINE_REQUESTS_FETCH: queries.fetchRequestsEffect,
		TIMELINE_REQUESTS_QUERY_STARTED: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.timelineRequestsQueryStartedEffect
		},
		TIMELINE_REQUESTS_QUERY_SUCCEEDED: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.timelineRequestsQuerySucceededEffect
		},
		TIMELINE_REQUESTS_QUERY_FAILED: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.timelineRequestsQueryFailedEffect
		},
		TIMELINE_RECORD_WATCHER_SUBSCRIBE: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.timelineRecordWatcherSubscribeEffect
		},
		TIMELINE_RECORD_WATCHER_ITEM_CHANGED: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.timelineRecordWatcherItemChangedEffect
		},
		TIMELINE_RECORD_WATCHER_SUBSCRIBED: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.timelineRecordWatcherEmptyEffect
		},
		TIMELINE_RECORD_WATCHER_SUBSCRIBE_FAILED: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.timelineRecordWatcherEmptyEffect
		},
		TIMELINE_RECORD_WATCHER_UNSUBSCRIBED: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.timelineRecordWatcherEmptyEffect
		},
		TIMELINE_RECORD_WATCHER_SUBSCRIPTION_CHANGED: amb.recordWatcherEffect,
		[COMPONENT_DOM_READY]: actionHandlers.timelineComponentReadyEffect,
		[COMPONENT_CONNECTED]: actionHandlers.timelineComponentConnectedEffect,
		[COMPONENT_DISCONNECTED]:
			actionHandlers.timelineComponentDisconnectedEffect,
		[COMPONENT_PROPERTY_CHANGED]:
			actionHandlers.timelineComponentPropertyChangedEffect
	}
});
