import {createCustomElement, actionTypes} from '@servicenow/ui-core';

// graphQL
import queries from './graphql-queries';

// action handlers
import * as actionHandlers from './action-handlers';

// html
import styles from './styles.scss';
import view from './view';

// constants
import {VIEW_DATA, ACTION_HANDLERS_COMMON_CONFIG} from './constants';

// Component lifecycle actions
const {COMPONENT_DOM_READY, COMPONENT_PROPERTY_CHANGED} = actionTypes;

// attempt to stop repeating common config options everywhere
const actionHandlersCommonConfig = ACTION_HANDLERS_COMMON_CONFIG;

createCustomElement('sn-cmdb-relationship-card', {
	styles,
	renderer: {
		view: view
	},
	initialState: {
		page: 0,
		relationshipData: null,
		displayType: VIEW_DATA.DATA_LOADING
	},
	properties: {
		sysid: {
			default: ''
		},
		// Not actually used for relationships data
		table: {
			default: ''
		},
		// Not actually used for relationships data
		name: {
			default: ''
		}
	},
	actionHandlers: {
		RELATIONSHIP_FETCH_DATA: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.relationshipFetchDataEffect
		},
		RELATIONSHIP_FETCH: queries.relationshipQueryEffect,
		RELATIONSHIP_QUERY_STARTED: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.relationshipQueryStartedEffect
		},
		RELATIONSHIP_QUERY_SUCCEEDED: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.relationshipQuerySucceededEffect
		},
		RELATIONSHIP_QUERY_FAILED: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.relationshipQueryFailedEffect
		},
		RELATIONSHIP_SET_PAGE: {
			...actionHandlersCommonConfig,
			effect: actionHandlers.relationshipSetPageEffect
		},
		// **** Page component handlers ****
		'NOW_BUTTON_BARE#CLICKED': {
			...actionHandlersCommonConfig,
			effect: actionHandlers.handleBareButtonClickEffect
		},
		[COMPONENT_DOM_READY]: actionHandlers.componentReadyEffect,
		[COMPONENT_PROPERTY_CHANGED]: actionHandlers.componentPropertyChangedEffect
	}
});
