// constants
import {ACTIONS, ERROR_CODES, VIEW_DATA} from './constants';

export function relationshipFetchDataEffect(coeffects) {
	const {state, dispatch} = coeffects;
	const {sysid} = state.properties;
	dispatch(ACTIONS.RELATIONSHIP_FETCH, {
		queryConditions: `childISNOTEMPTY^parentISNOTEMPTY^parent=${sysid}^ORchild=${sysid}`,
		pagination: {limit: 150, offset: 0}
	});
}

export function relationshipQueryStartedEffect(coeffects) {
	const {updateState} = coeffects;
	// reset loading and error indicators
	updateState({
		displayType: VIEW_DATA.DATA_LOADING,
		relationshipData: []
	});
}

export function relationshipQuerySucceededEffect(coeffects) {
	const {action, updateState} = coeffects;
	const rowCount = action.payload.data.GlideRecord_Query.relationshipsLevelOne
		? action.payload.data.GlideRecord_Query.relationshipsLevelOne._rowCount
		: 0;
	const results = action.payload.data.GlideRecord_Query.relationshipsLevelOne
		? action.payload.data.GlideRecord_Query.relationshipsLevelOne._results
		: [];
	const displayType =
		rowCount > 150
			? ERROR_CODES.DATA_TOO_LONG
			: results && results.length > 0
			? ''
			: ERROR_CODES.NO_DATA_LOADED;
	updateState({
		relationshipData: results,
		displayType: displayType
	});
}

export function relationshipQueryFailedEffect(coeffects) {
	const {updateState} = coeffects;
	updateState({
		displayType: ERROR_CODES.DATA_LOADING_ERROR
	});
}

export function relationshipSetPageEffect(coeffects) {
	const {action, state, updateState} = coeffects;
	const {newPage} = action.payload;
	const {page} = state;

	// no need to rerender if already active
	if (page === newPage) {
		return;
	}

	// set new page
	updateState({page: newPage});
}

export function handleBareButtonClickEffect(coeffects) {
	const {dispatch, action, state} = coeffects;
	const {pageAction, totalPages, relationship} = action.payload;
	const {page} = state;
	const {sysid} = state.properties;

	switch (pageAction) {
		case VIEW_DATA.DEPENDENCY_VIEW:
			// TODO: NEEDS TO BE TESTED IN AGENT WORKSPACE
			window.open(`/$ngbsm.do?id=${sysid}&level=3`, '_blank');
			break;
		case VIEW_DATA.VIEW_RELATIONSHIP:
			// TODO: NEEDS TO BE TESTED IN AGENT WORKSPACE
			dispatch(ACTIONS.ITEM_SELECTED, {
				table: relationship.childSysClassName,
				sys_id: relationship.childSysId
			});
			break;
		case VIEW_DATA.PREVIOUS_PAGE:
			dispatch(ACTIONS.RELATIONSHIP_SET_PAGE, {
				newPage: page === 0 ? totalPages - 1 : page - 1
			});
			break;
		case VIEW_DATA.NEXT_PAGE:
		default:
			dispatch(ACTIONS.RELATIONSHIP_SET_PAGE, {
				newPage: page === totalPages - 1 ? 0 : page + 1
			});
			break;
	}
}

export function componentReadyEffect(coeffects) {
	const {dispatch} = coeffects;
	dispatch(ACTIONS.RELATIONSHIP_FETCH_DATA);
}

export function componentPropertyChangedEffect(coeffects) {
	const {action, dispatch} = coeffects;

	// refetch data and set to first page if the sysid is changed
	const {name, previousValue} = action.payload;

	if (name === 'sysid' && previousValue) {
		dispatch(ACTIONS.RELATIONSHIP_SET_PAGE, {newPage: 0});
		dispatch(ACTIONS.RELATIONSHIP_FETCH_DATA);
	}
}
