import snabbdom from '@seismic/snabbdom-renderer';

// constants
import {ACTIONS, ERROR_CODES, VIEW_DATA, ICONS} from './constants';

// HTML Rendering Methods
import {
	getPageDisplayMessage,
	getFormattedRelationships,
	getRelationshipsDisplayData
} from './helpers';

// UI Components
import '@servicenow/now-button';
import '@servicenow/now-icon';
import {t} from 'sn-translate';

/* eslint-disable-next-line no-unused-vars */
const {Fragment} = snabbdom;

const getDependencyViewHtml = () => {
	return (
		<now-button-bare
			icon-end={ICONS.EXTERNAL_LINK}
			label={t('Dependency View')}
			size="sm"
			variant="primary"
			hidePadding={true}
			config-aria={{'aria-label': t('Dependency View')}}
			append-to-payload={{
				pageAction: VIEW_DATA.DEPENDENCY_VIEW
			}}></now-button-bare>
	);
};

const getErrorMessageContentHtml = (errorDisplay) => {
	const {message, icon, spin} = errorDisplay;
	const spinAttr = spin ? {spin: true} : {};

	return (
		<div className="sn-widget--body">
			<div className="sn-widget--row">
				<div
					className="sn-widget--column is-error cmdb-widget--tabindex"
					tabIndex="0"
					aria-label={message}>
					<now-icon {...spinAttr} icon={icon} size="md"></now-icon>
					<span>{message}</span>
				</div>
			</div>
		</div>
	);
};

const getRelationshipContentHtml = (displayData, page, dispatch) => {
	let footerHtml,
		prevPageHtml,
		nextPageHtml = '';

	const relationshipHtml = getRelationshipEntries(
		displayData.ciRelations,
		page
	);

	if (displayData.totalPages > 1) {
		footerHtml = getFooterHtml(page, displayData.totalPages, dispatch);
		prevPageHtml = getPageNavButtonHtml(
			t('Previous Page'),
			VIEW_DATA.PREVIOUS_PAGE,
			ICONS.PREVIOUS_PAGE,
			displayData.totalPages
		);
		nextPageHtml = getPageNavButtonHtml(
			t('Next Page'),
			VIEW_DATA.NEXT_PAGE,
			ICONS.NEXT_PAGE,
			displayData.totalPages
		);
	}

	return (
		<Fragment>
			<div className="sn-widget--body snu-p-0">
				<div className="sn-widget--row has-slider">
					<div className="sn-slider">
						{prevPageHtml}
						<div className="sn-relationshipcard cmdb-ci-relationshipcard--truncate">
							<div className="cmdb-widget--rels-list-container">
								{relationshipHtml}
							</div>
						</div>
						{nextPageHtml}
					</div>
				</div>
			</div>
			{footerHtml}
		</Fragment>
	);
};

const getRelationshipEntries = (rels, page) => {
	let entries = [];

	rels[page].map((rel) =>
		entries.push(
			<ul
				className="cmdb-widget--rel-type-list"
				key={rel.relationTypeSysId + rel.ciClassDisplayName}>
				<li key={rel.relationTypeSysId}>
					{rel.relationTypeDesc} -{' '}
					<span className="cmdb-widget--rels-bold">
						{rel.ciClassDisplayName} ({rel.relCount})
					</span>
				</li>
				<ul
					className="cmdb-widget--rel-list"
					key={rel.relationTypeSysId + rel.childSysId}>
					{rel.relations.map((relation) => (
						<li>
							<now-button-bare
								key={relation.childSysId}
								hide-padding
								label={relation.childName}
								size="md"
								variant="primary"
								className="cmdb-widget--tabindex"
								config-aria={{'aria-label': relation.childName}}
								append-to-payload={{
									pageAction: VIEW_DATA.VIEW_RELATIONSHIP,
									relationship: relation
								}}></now-button-bare>
						</li>
					))}
				</ul>
			</ul>
		)
	);

	return entries;
};

const getFooterHtml = (selectedIndex, totalPages, dispatch) => {
	const navDots = [];

	for (let dotIndex = 0; dotIndex < totalPages; dotIndex++) {
		// Note: <now-button-iconic> adds padding and hover/select styling that conflicts with this layout/design
		navDots.push(
			<li key={dotIndex}>
				<button
					type="button"
					aria-label={t('Page {0} out of {1}', dotIndex + 1, totalPages)}
					class={{
						'sn-slider--page': true,
						'is-active': dotIndex === selectedIndex
					}}
					key={dotIndex}
					on-click={() =>
						dispatch(ACTIONS.RELATIONSHIP_SET_PAGE, {newPage: dotIndex})
					}>
					{dotIndex + 1}
				</button>
			</li>
		);
	}

	return (
		<div className="sn-widget--footer">
			<ul className="sn-slider--pages">{navDots}</ul>
		</div>
	);
};

const getPageNavButtonHtml = (label, action, icon, totalPages) => {
	return (
		<now-button-bare
			icon-end={icon}
			config-aria={{'aria-label': label}}
			size="sm"
			variant="secondary"
			className="sn-btn sn-btn__default-borderless sn-btn__sm sn-btn__icon"
			append-to-payload={{
				pageAction: action,
				totalPages: totalPages
			}}></now-button-bare>
	);
};

/** @seismicView sn-cmdb-relationship-card */
const view = (state, dispatch) => {
	const {page, displayType, relationshipData} = state;
	const {sysid} = state.properties;
	let errorDisplay = {
		message: null,
		icon: ICONS.ERROR_INDICATOR,
		spin: false
	};
	let displayData = {ciRelations: null, totalPages: 1};
	let contentDisplayHtml;
	let dependencyViewHtml = '';

	if (displayType === VIEW_DATA.DATA_LOADING) {
		errorDisplay.message = getPageDisplayMessage(VIEW_DATA.DATA_LOADING);
		errorDisplay.icon = ICONS.LOADING_INDICATOR;
		errorDisplay.spin = true;
	} else if (
		displayType === ERROR_CODES.DATA_LOADING_ERROR ||
		displayType === ERROR_CODES.NO_DATA_LOADED ||
		displayType === ERROR_CODES.DATA_TOO_LONG
	) {
		errorDisplay.message = getPageDisplayMessage(displayType);
	} else {
		// No error or loading message so process the data
		const relationships = getFormattedRelationships(relationshipData, sysid);
		displayData = getRelationshipsDisplayData(relationships);

		// if no data here then the component lifecycle is still running and Loading
		if (displayData.totalPages === 0) {
			errorDisplay.message = getPageDisplayMessage(VIEW_DATA.DATA_LOADING);
			errorDisplay.icon = ICONS.LOADING_INDICATOR;
			errorDisplay.spin = true;
		}
	}

	// get the main content display based on loaded data and state
	if (errorDisplay.message) {
		contentDisplayHtml = getErrorMessageContentHtml(errorDisplay);
	} else {
		contentDisplayHtml = getRelationshipContentHtml(
			displayData,
			page,
			dispatch
		);
		dependencyViewHtml = getDependencyViewHtml();
	}

	return (
		<div className="sn-widget">
			<div className="sn-widget--header cmdb-ci-relationshipcard--truncate">
				<h2
					className="sn-widget--header--content cmdb-widget--tabindex"
					aria-label={t('Level 1 Relationships')}>
					{t('Level 1 Relationships')}
				</h2>
				{dependencyViewHtml}
			</div>
			{contentDisplayHtml}
		</div>
	);
};

export default view;
