// helper methods
import {
	getLoadingState,
	getFormattedActivityData,
	getTimestampForNumberOfDays,
	safeParseInt
} from './helpers';

// UI components
import 'sn-component-timeline';
import {t} from 'sn-translate';

// use instead of property to set loading and activityData
// this avoids excessive call to render when updating properties
const renderMap = new WeakMap();

/** @seismicView sn-cmdb-timeline-card */
const view = (state, dispatch) => {
	const {days} = state.properties;
	const {
		historyResponseData,
		historyError,
		incidentsResponseData,
		incidentsResponseCount,
		incidentsError,
		requestsResponseData,
		requestsResponseCount,
		requestsError,
		timestamp,
		activityDataKey
	} = state;

	// setup display dates with padding outside the number of days for handling display of events that fall
	// on the start or end of the date range
	const minStartDate = getTimestampForNumberOfDays(
		safeParseInt(days) + 1,
		false
	);
	const maxEndDate = getTimestampForNumberOfDays(1, true);

	const timelineAttributes = {
		label: t('CI Timeline - Last {0} days', days),
		isEmpty: false,
		emptyMessage: null,
		isError: false,
		errorMessage: null,
		isLoading: false,
		loadingMessage: null,
		eventData: [],
		startDate: minStartDate,
		endDate: maxEndDate,
		legendEventTitle: t('CI Timeline Events'),
		minZoomSpread: 'S'
	};

	const isLoading = getLoadingState(state);

	if (isLoading) {
		timelineAttributes.isLoading = true;
		timelineAttributes.loadingMessage = t('Loading Timeline data...');

		// reset any data that's already loaded
		if (activityDataKey) {
			renderMap.set(activityDataKey, null);
		}
	} else if (historyError || incidentsError || requestsError) {
		timelineAttributes.isError = true;
		timelineAttributes.errorMessage = t(
			'Data transaction timed out. Please refresh the page.'
		);
	} else if (
		incidentsResponseCount !== incidentsResponseData.length ||
		requestsResponseCount !== requestsResponseData.length
	) {
		timelineAttributes.isError = true;
		timelineAttributes.errorMessage = t(
			'The maximum number of changes that can be displayed has been reached. To view the complete list, open the CI Dashboard.'
		);
	} else {
		// Note: activityData is set to null whenever a request is made to retrieve data
		let activityData = activityDataKey ? renderMap.get(activityDataKey) : null;

		// format the responses into activity data for timeline
		if (!activityData) {
			activityData = getFormattedActivityData(
				historyResponseData,
				incidentsResponseData,
				requestsResponseData,
				timestamp
			);

			renderMap.set(activityDataKey, activityData);
		}

		// check to see if there were any events actually retured
		let eventCount = 0;

		if (activityData.length > 0) {
			activityData.forEach((activity) => {
				eventCount += activity.events.length;
			});
		}

		if (activityData.length === 0 || eventCount === 0) {
			timelineAttributes.isEmpty = true;
			timelineAttributes.emptyMessage = t('No data available');
		} else if (activityData.length > 200 || eventCount > 200) {
			timelineAttributes.isError = true;
			timelineAttributes.errorMessage = t(
				'The maximum number of changes that can be displayed has been reached. To view the complete list, open the CI Dashboard.'
			);
		} else {
			timelineAttributes.eventData = activityData;
		}
	}

	return (
		<div className="sn-widget sn-cmdb-timeline-card">
			<div className="sn-widget--body">
				<sn-component-timeline {...timelineAttributes}></sn-component-timeline>
			</div>
		</div>
	);
};

export default view;
