// UI Components
import '@servicenow/now-button';
import '@servicenow/now-icon';
import {t} from 'sn-translate';

// constants
import {ACTIONS, VIEW_DATA, ERROR_CODES, ICONS} from './constants';

// helpers
import * as helpers from './helpers';

const getNavDotsHtml = (selectedIndex, dispatch) => {
	const navDots = [];

	for (let dotIndex = 0; dotIndex < 5; dotIndex++) {
		// Note: <now-button-iconic> adds padding and hover/select styling that conflicts with this layout/design
		navDots.push(
			<li key={dotIndex}>
				<button
					type="button"
					aria-label={t('Health Widget {0} out of 5', dotIndex + 1)}
					class={{
						'sn-slider--page': true,
						'is-active': dotIndex === selectedIndex
					}}
					key={dotIndex}
					on-click={() =>
						dispatch(ACTIONS.HEALTH_SET_DISPLAY_INDEX, {newIndex: dotIndex})
					}>
					{dotIndex}
				</button>
			</li>
		);
	}

	return navDots;
};

const getPageDisplayLabelHtml = (dataDisplay, displayIndex) => {
	const {classHealthUpdOn, ciMetricCount, ciMetricTotalCount} = dataDisplay;
	const currentDateTime = helpers.getFormattedCurrentDate();
	const pageDisplayLabel = [];
	const lastUpdatedOnDisplay = `${t('Last updated')} ${
		classHealthUpdOn ? classHealthUpdOn : currentDateTime
	}`;

	const countDisplayText = helpers.getCountDisplayText(
		displayIndex,
		ciMetricCount,
		ciMetricTotalCount
	);

	// add in the count display
	// TODO: REMOVED - dangerouslySetInnerHTML={{__html: count}} - WAS THIS EVEN NECESSARY?
	pageDisplayLabel.push(
		<div
			key={countDisplayText.countDisplay}
			className="sn-ci-health-card---data">
			{countDisplayText.countDisplay}
		</div>
	);

	// add in the total display
	pageDisplayLabel.push(
		<div
			key={countDisplayText.totalCountDisplay}
			className="sn-ci-health-card---tot-data">
			{countDisplayText.totalCountDisplay}
		</div>
	);

	pageDisplayLabel.push(
		<div key={lastUpdatedOnDisplay} className="sn-ci-health-card---updated">
			{lastUpdatedOnDisplay}
		</div>
	);

	return (
		<div
			className="sn-ci-health-card---info-container"
			aria-label={`${countDisplayText.countDisplay}${countDisplayText.totalCountDisplay}`}>
			{pageDisplayLabel}
		</div>
	);
};

const getErrorMessageContentHtml = (errorDisplay, isLoading) => {
	const {message, icon} = errorDisplay;
	const spinAttr = isLoading ? {spin: true} : {};

	return (
		<div
			className="sn-widget--column is-error cmdb-ci-healthcard--tabindex"
			tabIndex="-1"
			aria-label={message}>
			<now-icon {...spinAttr} icon={icon} size="md"></now-icon>
			<span>{message}</span>
		</div>
	);
};

const getSvgContentHtml = (dataDisplay, displayIndex) => {
	// setup the svg data based on the display data
	const pageLabelHtml = getPageDisplayLabelHtml(dataDisplay, displayIndex);

	return (
		<div className="sn-healthcard cmdb-ci-healthcard--truncate" tabIndex="-1">
			<div className="chart-container">
				{helpers.getSvgElement(dataDisplay, 'cmdb-health-img')}
				<div className="radial-inner-bg"></div>
			</div>
			{pageLabelHtml}
		</div>
	);
};

/** @seismicView sn-cmdb-health-card */
const view = (state, dispatch) => {
	const {displayIndex} = state;
	const {table, sysid} = state.properties;

	const {
		criticalIncidentsCount,
		criticalIncidentsErrors,
		totalIncidentsCount,
		totalIncidentsErrors,
		criticalChangesCount,
		criticalChangesErrors,
		totalChangesCount,
		totalChangesErrors,
		reqAttrStatusData,
		reqAttrStatusErrors,
		nonCompliantRelStatusData,
		nonCompliantRelStatusErrors,
		staleRelHealthStatusData,
		staleRelHealthStatusErrors,
		reqAttrCountData,
		reqAttrCountErrors,
		totalAttrCountData,
		totalAttrCountErrors,
		nonCompliantRelCountData,
		nonCompliantRelCountErrors,
		totalRelCount,
		totalRelCountErrors,
		staleRelCountData,
		staleRelCountErrors
	} = state;

	const isLoading = helpers.getLoadingState(state);
	let errorDisplay = {message: null, icon: ICONS.ERROR_INDICATOR};
	let dataDisplay = {
		classHealthStatus: null,
		ciMetricCount: null,
		ciMetricTotalCount: null,
		classHealthUpdOn: null
	};
	let contentDisplayHtml;

	const navDots = getNavDotsHtml(displayIndex, dispatch);

	// determine what to display here
	if (isLoading) {
		errorDisplay.message = helpers.getPageDisplayMessage(
			VIEW_DATA.DATA_LOADING,
			displayIndex
		);
		errorDisplay.icon = ICONS.LOADING_INDICATOR;
	} else {
		switch (displayIndex) {
			// Critical change requests vs Total change requests
			case 1:
				if (criticalChangesErrors || totalChangesErrors) {
					errorDisplay.message = helpers.getPageDisplayMessage(
						ERROR_CODES.TRANSACTION_ERROR,
						displayIndex
					);
				} else {
					dataDisplay.ciMetricCount = criticalChangesCount;
					dataDisplay.ciMetricTotalCount = totalChangesCount;
				}
				break;
			// Incomplete attributes vs Total attributes
			case 2:
				if (reqAttrStatusErrors || reqAttrCountErrors || totalAttrCountErrors) {
					errorDisplay.message = helpers.getPageDisplayMessage(
						ERROR_CODES.TRANSACTION_ERROR,
						displayIndex
					);
				} else {
					dataDisplay.classHealthStatus = helpers.processHealthStatusData(
						reqAttrStatusData
					);
					dataDisplay.ciMetricCount = helpers.processReqAttrCountData(
						reqAttrCountData
					);
					dataDisplay.ciMetricTotalCount = helpers.processTotalAttrCountData(
						totalAttrCountData
					);
					dataDisplay.classHealthUpdOn = helpers.processHealthUpdOnData(
						reqAttrStatusData
					);
				}
				break;
			// Non-compliant relationships vs Total relationships
			case 3:
				if (
					(nonCompliantRelStatusErrors,
					nonCompliantRelCountErrors,
					totalRelCountErrors)
				) {
					errorDisplay.message = helpers.getPageDisplayMessage(
						ERROR_CODES.TRANSACTION_ERROR,
						displayIndex
					);
				} else {
					dataDisplay.classHealthStatus = helpers.processHealthStatusData(
						nonCompliantRelStatusData
					);
					dataDisplay.ciMetricCount = helpers.processNonCompliantRelCountData(
						nonCompliantRelCountData
					);
					dataDisplay.ciMetricTotalCount = totalRelCount;
					dataDisplay.classHealthUpdOn = helpers.processHealthUpdOnData(
						nonCompliantRelStatusData
					);
				}
				break;
			case 4:
				if (
					(staleRelHealthStatusErrors, staleRelCountErrors, totalRelCountErrors)
				) {
					errorDisplay.message = helpers.getPageDisplayMessage(
						ERROR_CODES.TRANSACTION_ERROR,
						displayIndex
					);
				} else {
					dataDisplay.classHealthStatus = helpers.processHealthStatusData(
						staleRelHealthStatusData
					);
					dataDisplay.ciMetricCount = helpers.processStaleRelCountData(
						staleRelCountData
					);
					dataDisplay.ciMetricTotalCount = totalRelCount;
					dataDisplay.classHealthUpdOn = helpers.processHealthUpdOnData(
						staleRelHealthStatusData
					);

					// Show error if total relationships count is more than 1000 (pagination limit)
					if (
						dataDisplay.ciMetricTotalCount &&
						dataDisplay.ciMetricTotalCount > 1000
					) {
						errorDisplay.message = helpers.getPageDisplayMessage(
							ERROR_CODES.ERROR_TOO_LONG,
							displayIndex
						);
					}
				}
				break;
			// Critical incidents vs Total incidents
			// case 0
			default:
				if (criticalIncidentsErrors || totalIncidentsErrors) {
					errorDisplay.message = helpers.getPageDisplayMessage(
						ERROR_CODES.TRANSACTION_ERROR,
						displayIndex
					);
				} else {
					dataDisplay.ciMetricCount = criticalIncidentsCount;
					dataDisplay.ciMetricTotalCount = totalIncidentsCount;
				}
				break;
		}
	}

	// Verify the health job was run
	if (
		dataDisplay.classHealthStatus !== null &&
		dataDisplay.classHealthStatus !== VIEW_DATA.HEALTH_STATUS_COMPLETE
	) {
		errorDisplay.message = helpers.getPageDisplayMessage(
			ERROR_CODES.JOB_NOT_RUN,
			displayIndex
		);
	}

	// get the main content display based on loaded data and state
	if (errorDisplay.message) {
		contentDisplayHtml = getErrorMessageContentHtml(errorDisplay, isLoading);
	} else {
		contentDisplayHtml = getSvgContentHtml(dataDisplay, displayIndex);
	}

	return (
		<div className="sn-widget sn-cmdb-health-card">
			<div className="sn-widget--header cmdb-ci-healthcard--truncate">
				<h2
					className="sn-widget--header--content cmdb-widget--tabindex"
					aria-label={t('CI Health')}>
					{t('CI Health')}
				</h2>
				<now-button-bare
					icon-end={ICONS.EXTERNAL_LINK}
					label={t('CI Dashboard')}
					size="sm"
					variant="primary"
					hidePadding={true}
					append-to-payload={{
						pageAction: VIEW_DATA.VIEW_DASHBOARD
					}}></now-button-bare>
			</div>
			<div className="sn-widget--body snu-p-0">
				<div className="sn-widget--row has-slider">
					<div className="sn-slider">
						<now-button-bare
							icon-end={ICONS.PREVIOUS_PAGE}
							config-aria={{'aria-label': t('Previous Health Widget')}}
							size="sm"
							variant="secondary"
							className="sn-btn sn-btn__default-borderless sn-btn__sm sn-btn__icon"
							append-to-payload={{
								pageAction: VIEW_DATA.PREVIOUS_PAGE
							}}></now-button-bare>
						{contentDisplayHtml}
						<now-button-bare
							icon-end={ICONS.NEXT_PAGE}
							config-aria={{'aria-label': t('Next Health Widget')}}
							size="sm"
							variant="secondary"
							className="sn-btn sn-btn__default-borderless sn-btn__sm sn-btn__icon"
							append-to-payload={{
								pageAction: VIEW_DATA.NEXT_PAGE
							}}></now-button-bare>
					</div>
				</div>
			</div>
			<div className="sn-widget--footer">
				<ul className="sn-slider--pages">{navDots}</ul>
			</div>
		</div>
	);
};

export default view;
