import snabbdom from '@seismic/snabbdom-renderer';
import {t} from 'sn-translate';
import {VIEW_DATA, ERROR_CODES} from './constants';

const {createElementFromNode} = snabbdom;

export function processHealthStatusData(statusData) {
	return statusData && statusData.length === 1
		? statusData[0].status.value
		: '';
}

export function processReqAttrCountData(reqAttrCountData) {
	const failureMessage = 'Following required fields are not populated\n';

	if (!reqAttrCountData || reqAttrCountData.length === 0) {
		return 0;
	}

	let data = reqAttrCountData[0].description.value;

	if (data.indexOf(failureMessage) !== -1) {
		data = data.replace(failureMessage, '');
	}

	if (data.indexOf('\n') !== -1) {
		const columnsData = data.split('\n');
		return columnsData.length;
	}

	return 1;
}

export function processTotalAttrCountData(totalAttrCountData) {
	if (!totalAttrCountData || totalAttrCountData.length === 0) {
		return 0;
	}

	const filtered = totalAttrCountData.filter((item) => {
		return item.name !== 'sys_tags';
	});

	return filtered.length;
}

export function processHealthUpdOnData(healthStatusData) {
	return healthStatusData !== null &&
		healthStatusData.length === 1 &&
		healthStatusData[0].status.value === VIEW_DATA.HEALTH_STATUS_COMPLETE
		? healthStatusData[0].started_on.displayValue
		: t('N/A');
}

export function processStaleRelCountData(countData) {
	let count = 0;
	let countIndex = 0;

	while (countIndex < countData.length) {
		if (
			countData[countIndex]._query.queryWithSysId.cmdb_health_result._rowCount >
			0
		) {
			count++;
		}

		countIndex++;
	}

	return count;
}

export function processNonCompliantRelCountData(countData) {
	let count = 0;
	let countIndex = 0;

	while (countIndex < countData.length) {
		count += countData[countIndex]._query.queryWithSysId.cmdb_rel_ci._rowCount;
		countIndex++;
	}

	return count;
}

export function getCountDisplayText(displayIndex, count, totalCount) {
	let countDisplay;
	let totalCountDisplay;

	// TODO: This is really bad usage of translations. Should not be separated between 2 lines.
	// get title to display for selected display index
	switch (displayIndex) {
		case 1:
			countDisplay = `${count} ${t('Critical Change Requests')}`;
			totalCountDisplay = `${t('of')} ${totalCount} ${t(
				'Total Change Requests'
			)}`;
			break;
		case 2:
			countDisplay = `${count} ${t('Incomplete Attributes')}`;
			totalCountDisplay = `${t('of')} ${totalCount} ${t('Total Attributes')}`;
			break;
		case 3:
			countDisplay = `${count} ${t('Non-compliant Relationships')}`;
			totalCountDisplay = `${t('of')} ${totalCount} ${t(
				'Total Relationships'
			)}`;
			break;
		case 4:
			countDisplay = `${count} ${t('Stale Relationships')}`;
			totalCountDisplay = `${t('of')} ${totalCount} ${t(
				'Total Relationships'
			)}`;
			break;
		// Page 0
		default:
			countDisplay = `${count} ${t('Critical Incidents')}`;
			totalCountDisplay = `${t('of')} ${totalCount} ${t('Total Incidents')}`;
			break;
	}

	return {
		countDisplay: countDisplay,
		totalCountDisplay: totalCountDisplay
	};
}

export function getFormattedCurrentDate() {
	const time = new Date();
	return `${time.getFullYear()}-${addDateZero(
		time.getMonth() + 1
	)}-${addDateZero(time.getDate())} ${addDateZero(
		time.getHours()
	)}:${addDateZero(time.getMinutes())}:${addDateZero(time.getSeconds())}`;
}

// *** Private ***
const addDateZero = (dateInt) => {
	return dateInt < 10 ? `0${dateInt}` : dateInt;
};

export function getPageDisplayMessage(msgType, displayIndex) {
	switch (msgType) {
		case VIEW_DATA.DATA_LOADING:
			return t('Loading CI Health data...');
		case ERROR_CODES.JOB_NOT_RUN:
			return getJobNotRunMessage(displayIndex);
		case ERROR_CODES.TRANSACTION_ERROR:
			return t('Data transaction timed out. Refresh the page to try again.');
		case ERROR_CODES.ERROR_TOO_LONG:
			return t(
				'The max number of relationships that can be queried has been reached. To view relationships health score results, open the CI dashboard.'
			);
		default:
			return t('No data available.');
	}
}

// *** Private ***
const getJobNotRunMessage = (displayIndex) => {
	switch (displayIndex) {
		case 2:
			return t(
				'Run CMDB Health Dashboard jobs to view incomplete attributes score.'
			);
		case 3:
			return t(
				'Run CMDB Health Dashboard jobs to view non-compliant relationships score.'
			);
		case 4:
			return t(
				'Run CMDB Health Dashboard jobs to view stale relationships score.'
			);
		default:
			return t('No data available.');
	}
};

export function getLoadingState(state) {
	const {
		criticalIncidentsLoading,
		totalIncidentsLoading,
		criticalChangesLoading,
		totalChangesLoading,
		reqAttrStatusLoading,
		nonCompliantRelStatusLoading,
		staleRelHealthStatusLoading,
		reqAttrCountLoading,
		totalAttrCountLoading,
		nonCompliantRelCountLoading,
		totalRelCountLoading,
		staleRelCountLoading
	} = state;

	return (
		criticalIncidentsLoading ||
		totalIncidentsLoading ||
		criticalChangesLoading ||
		totalChangesLoading ||
		reqAttrStatusLoading ||
		nonCompliantRelStatusLoading ||
		staleRelHealthStatusLoading ||
		reqAttrCountLoading ||
		totalAttrCountLoading ||
		nonCompliantRelCountLoading ||
		totalRelCountLoading ||
		staleRelCountLoading
	);
}

// HACK: Render svg inline in order for correct display parsing. This is a known seismic issue.
export function getSvgElement(dataDisplay, cssClass) {
	const svgData = getSvgDisplayData(dataDisplay);
	const wrapper = document.createElement('SPAN');

	wrapper.innerHTML = `<svg
		xmlns="http://www.w3.org/2000/svg"
		viewBox="-1 -1 2 2"
		class="${cssClass}">
			<path id="${svgData.passArcId}" d="${svgData.passArcPath}" class="${svgData.passArcId}" />
			<path id="borderArc1" d="${svgData.borderArc1}" class="borderArc1" />
			<path id="failArc" d="${svgData.failArcPath}" class="failArc" />
			<path id="borderArc2" d="${svgData.borderArc2}" class="borderArc2" />
		</svg>
	`;

	const vnode = createElementFromNode(wrapper);
	return vnode.children[0];
}

// *** Private ***
const getSvgDisplayData = (dataDisplay) => {
	const {ciMetricTotalCount} = dataDisplay;
	const displayPercentage = getDisplayPercentage(dataDisplay);

	let borderPercentage = 0.015;
	let failedPercentage = displayPercentage;
	let cumulativePercent = 0;
	let passArcPath;
	let failArcPath;
	let borderArc1;
	let borderArc2;
	let passArcId = 'passArcGreen';

	if (displayPercentage === 0 || displayPercentage === 1) {
		borderPercentage = 0;
	} else {
		if (failedPercentage > 2 * borderPercentage) {
			failedPercentage = displayPercentage - 2 * borderPercentage;
		} else {
			// Passing 0.01 as failed percentage for cases where failed percentage is less than or equal to border percentage
			// in order to show atleast a line of failed color. If not, there is no representation at all, due to borders added around failed part.
			failedPercentage = 0.01;
		}
	}

	const slices = [
		{percent: 1 - displayPercentage},
		{percent: borderPercentage},
		{percent: failedPercentage},
		{percent: borderPercentage}
	];

	if (ciMetricTotalCount === 0) {
		passArcId = 'passArcGray';
	}

	for (let loopIndex = 0; loopIndex < slices.length; loopIndex++) {
		// destructuring assignment sets the two variables at once
		const [startX, startY] = getCoordinatesForPercent(cumulativePercent);

		// each slice starts where the last slice ended, so keep a cumulative percent
		cumulativePercent += slices[loopIndex].percent;

		const [endX, endY] = getCoordinatesForPercent(cumulativePercent);

		// if the slice is more than 50%, take the large arc (the long way around)
		const largeArcFlag = slices[loopIndex].percent > 0.5 ? 1 : 0;

		if (loopIndex === 0) {
			// create an array and join it just for code readability
			passArcPath = [
				`M ${startX} ${startY}`, // Move
				`A 1 1 0 ${largeArcFlag} 1 ${endX} ${endY}`, // Arc
				`L 0 0` // Line
			].join(' ');
			continue;
		}

		if (loopIndex === 1) {
			borderArc1 = [
				`M ${startX} ${startY}`, // Move
				`A 1 1 0 ${largeArcFlag} 1 ${endX} ${endY}`, // Arc
				`L 0 0` // Line
			].join(' ');
			continue;
		}

		if (loopIndex === 2) {
			failArcPath = [
				`M ${startX} ${startY}`, // Move
				`A 1 1 0 ${largeArcFlag} 1 ${endX} ${endY}`, // Arc
				`L 0 0` // Line
			].join(' ');
			continue;
		}

		// loopIndex === 3
		borderArc2 = [
			`M ${startX} ${startY}`, // Move
			`A 1 1 0 ${largeArcFlag} 1 ${endX} ${endY}`, // Arc
			`L 0 0` // Line
		].join(' ');
	}

	return {
		passArcId: passArcId,
		passArcPath: passArcPath,
		borderArc1: borderArc1,
		failArcPath: failArcPath,
		borderArc2: borderArc2
	};
};

// *** Private ***
const getDisplayPercentage = (dataDisplay) => {
	const {ciMetricCount, ciMetricTotalCount} = dataDisplay;

	const percentage =
		ciMetricTotalCount !== 0
			? (ciMetricCount / ciMetricTotalCount).toFixed(2)
			: 0;
	return parseFloat(percentage);
};

// *** Private ***
const getCoordinatesForPercent = (percent) => {
	const x = removeExponent(Math.cos(2 * Math.PI * percent));
	const y = removeExponent(Math.sin(2 * Math.PI * percent));
	return [x, y];
};

// *** Private ***
// Eliminate the exponent notation that breaks rendering in IE.
// Does not require a greater precision than a few decimals.
const removeExponent = (value) => {
	if (value < 0.0001 && value > 0) {
		value = 0.0001;
	} else if (value > -0.0001 && value < 0) {
		value = -0.0001;
	}

	return value;
};
