import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';

// constants
import {ACTIONS} from './constants';

// setup graphql queries for loading timeline data
// https://code.devsnc.com/pages/dev/sn-seismic/api-reference/effects/graphql-effect.html
// **** History ****
const historyQuery = `
	query ($sysId: String!, $table: String!, $timestamp: Long!) {
	  GlideActivity_Query {
		  getStream(table: $table, sysId: $sysId, timestamp: $timestamp) {
				entries {
					type
					sysCreatedOnAdjusted
					sysCreatedBy
					entryItem {
						... on GlideActivity_AuditType {
						auditValues {
							fieldName
							fieldLabel
							newValue
							oldValue
							}
						}
					}
				}
			}
		}
	}
`;

const fetchHistoryEffect = createGraphQLEffect(historyQuery, {
	variableList: ['sysId', 'table', 'timestamp'],
	startActionType: ACTIONS.TIMELINE_HISTORY_QUERY_STARTED,
	successActionType: ACTIONS.TIMELINE_HISTORY_QUERY_SUCCEEDED,
	errorActionType: ACTIONS.TIMELINE_HISTORY_QUERY_FAILED
});

// **** Incidents ****
const incidentsQuery = `
	query($queryConditions: String!, $pagination: Glide_PaginationInputType){
		GlideRecord_Query {
			incidentWithQueryConditions: incident(queryConditions: $queryConditions, pagination: $pagination ){
				_results{
					number {
						value
					}
					short_description {
						value
					}
					sys_created_on {
						value
					}
					priority{
						value
						displayValue
					}
					state{
						value
						displayValue
					}
					opened_by{
						_reference{
							name {
								value
							}
						}
					}
				}
				_rowCount
			}
		}
	}
`;

const fetchIncidentsEffect = createGraphQLEffect(incidentsQuery, {
	variableList: ['queryConditions', 'pagination'],
	startActionType: ACTIONS.TIMELINE_INCIDENTS_QUERY_STARTED,
	successActionType: ACTIONS.TIMELINE_INCIDENTS_QUERY_SUCCEEDED,
	errorActionType: ACTIONS.TIMELINE_INCIDENTS_QUERY_FAILED
});

// **** Requests ****
const requestsQuery = `
	query($queryConditions: String!, $pagination: Glide_PaginationInputType){
		GlideRecord_Query {
			changeWithQueryConditions: change_request(queryConditions: $queryConditions, pagination: $pagination ){
				_results{
					number {
						value
					}
					short_description {
						value
					}
					sys_created_on {
						value
					}
					state {
						value
					}
					opened_by{
						_reference{
							name {
								value
							}
						}
					}
				}
				_rowCount
			}
		}
	}
`;

const fetchRequestsEffect = createGraphQLEffect(requestsQuery, {
	variableList: ['queryConditions', 'pagination'],
	startActionType: ACTIONS.TIMELINE_REQUESTS_QUERY_STARTED,
	successActionType: ACTIONS.TIMELINE_REQUESTS_QUERY_SUCCEEDED,
	errorActionType: ACTIONS.TIMELINE_REQUESTS_QUERY_FAILED
});

export default {
	fetchHistoryEffect: fetchHistoryEffect,
	fetchIncidentsEffect: fetchIncidentsEffect,
	fetchRequestsEffect: fetchRequestsEffect
};
