// helper methods
import {
	getTimestampForNumberOfDays,
	formatJSDate,
	getFilterForSysId,
	safeParseInt
} from './helpers';

// constants
import {ACTIONS} from './constants';

export function timelineSetUniqueLocalsEffect(coeffects) {
	const {action, state, updateState} = coeffects;
	const {days} = state.properties;
	const {componentId} = action.payload;
	const timestamp = getTimestampForNumberOfDays(safeParseInt(days), false);
	const activityDataKey = {key: `activityData${componentId}`};

	updateState({
		timestamp: timestamp,
		activityDataKey: activityDataKey
	});
}

export function timelineFetchDataEffect(coeffects) {
	const {state, dispatch, updateState} = coeffects;
	const {table, sysid, active, isVisible} = state.properties;
	const {timestamp} = state;
	const historyDateString = formatJSDate(new Date(timestamp));
	const queryConditions = `cmdb_ci=${sysid}^sys_created_on>javascript:gs.dateGenerate(${historyDateString})`;

	// make sure component is visible and active before fetching data
	if (!(active && isVisible)) {
		updateState({
			historyLoading: false,
			incidentsLoading: false,
			requestsLoading: false
		});
		return;
	}

	dispatch(ACTIONS.TIMELINE_HISTORY_FETCH, {
		sysId: sysid,
		table: table,
		timestamp: timestamp
	});
	dispatch(ACTIONS.TIMELINE_INCIDENTS_FETCH, {
		queryConditions: queryConditions,
		pagination: {limit: 100, offset: 0}
	});
	dispatch(ACTIONS.TIMELINE_REQUESTS_FETCH, {
		queryConditions: queryConditions,
		pagination: {limit: 50, offset: 0}
	});
}

// *** History Data ***
export function timelineHistoryQueryStartedEffect(coeffects) {
	const {updateState} = coeffects;
	updateState({
		historyLoading: true,
		historyError: false,
		historyResponseData: null
	});
}

export function timelineHistoryQuerySucceededEffect(coeffects) {
	const {action, updateState} = coeffects;
	updateState({
		historyResponseData: action.payload.data.GlideActivity_Query.getStream
			? action.payload.data.GlideActivity_Query.getStream.entries
			: [],
		historyLoading: false
	});
}

export function timelineHistoryQueryFailedEffect(coeffects) {
	const {updateState} = coeffects;
	updateState({historyError: true, historyLoading: false});
}

// *** Incidents Data ***
export function timelineIncidentsQueryStartedEffect(coeffects) {
	const {updateState} = coeffects;
	updateState({
		incidentsLoading: true,
		incidentsError: false,
		incidentsResponseData: null,
		incidentsResponseCount: 0
	});
}

export function timelineIncidentsQuerySucceededEffect(coeffects) {
	const {action, updateState} = coeffects;
	updateState({
		incidentsResponseData:
			action.payload.data.GlideRecord_Query.incidentWithQueryConditions
				._results,
		incidentsResponseCount:
			action.payload.data.GlideRecord_Query.incidentWithQueryConditions
				._rowCount,
		incidentsLoading: false
	});
}

export function timelineIncidentsQueryFailedEffect(coeffects) {
	const {updateState} = coeffects;
	updateState({incidentsError: true, incidentsLoading: false});
}

// *** Requests Data ***
export function timelineRequestsQueryStartedEffect(coeffects) {
	const {updateState} = coeffects;
	updateState({
		requestsLoading: true,
		requestsError: false,
		requestsResponseData: null,
		requestsResponseCount: 0
	});
}

export function timelineRequestsQuerySucceededEffect(coeffects) {
	const {action, updateState} = coeffects;
	updateState({
		requestsResponseData:
			action.payload.data.GlideRecord_Query.changeWithQueryConditions._results,
		requestsResponseCount:
			action.payload.data.GlideRecord_Query.changeWithQueryConditions._rowCount,
		requestsLoading: false
	});
}

export function timelineRequestsQueryFailedEffect(coeffects) {
	const {updateState} = coeffects;
	updateState({requestsLoading: false, requestsError: true});
}

// *** Record Watcher ***
export function timelineRecordWatcherSubscribeEffect(coeffects) {
	const {action, state, dispatch} = coeffects;
	const {table, sysid} = state.properties;
	const {subscribe} = action.payload;
	const filter = getFilterForSysId(sysid);

	dispatch(ACTIONS.TIMELINE_RECORD_WATCHER_SUBSCRIPTION_CHANGED, {
		table,
		filter,
		subscribe
	});
}

export function timelineRecordWatcherItemChangedEffect(coeffects) {
	const {dispatch} = coeffects;

	// reload the data to pick up the latest change
	dispatch(ACTIONS.TIMELINE_FETCH_DATA);
}

export function timelineRecordWatcherEmptyEffect() {
	return true;
}

export function timelineComponentReadyEffect(coeffects) {
	const {
		dispatch,
		action: {
			payload: {host}
		}
	} = coeffects;

	// save off component id for unique settings
	const componentId = host.getAttribute('component-id');
	dispatch(ACTIONS.TIMELINE_SET_UNIQUE_LOCALS, {componentId: componentId});
	dispatch(ACTIONS.TIMELINE_FETCH_DATA);
}

export function timelineComponentConnectedEffect(coeffects) {
	const {dispatch} = coeffects;
	dispatch(ACTIONS.TIMELINE_RECORD_WATCHER_SUBSCRIBE, {subscribe: true});
}

export function timelineComponentDisconnectedEffect(coeffects) {
	const {dispatch} = coeffects;
	dispatch(ACTIONS.TIMELINE_RECORD_WATCHER_SUBSCRIBE, {subscribe: false});
}

export function timelineComponentPropertyChangedEffect(coeffects) {
	const {action, dispatch} = coeffects;

	// refetch data if the sysid is changed, component becomes active or visible after initial component ready
	const {name, previousValue, value} = action.payload;

	if (
		(name === 'sysid' && previousValue) ||
		((name === 'isVisible' || name === 'active') && value)
	) {
		dispatch(ACTIONS.TIMELINE_FETCH_DATA);
	}
}
